<template>
  <div class="seller" ref="seller">
     <div class="seller-content">
       <div class="overview">
         <h1 class="title">{{seller.name}}</h1>
         <div class="desc border-1px">
          <star :size="36" :score="seller.score"></star>
          <span class="text">({{seller.ratingCount}})</span>
          <span class="text">{{seller.sellCount}}</span>
         </div>
         <ul class="remark">
           <li class="block">
             <h2></h2>
             <div class="content">
               <span class="stress">
                 {{seller.minPrice}}
               </span>
             </div>
           </li>
           <li class="block">
            <h2></h2>
             <div class="content">
               <span class="stress">
                 {{seller.deliveryPrice}}
               </span>
             </div>
           </li>
           <li class="block">
            <h2></h2>
             <div class="content">
               <span class="stress">
                 {{seller.deliveryTime}}
               </span>
             </div>
           </li>
         </ul>
         <div class="favorite" @click="toggoleFavorite">
           <span class="icon-favorite" :class="{'active': favorite}"></span>
           <span class="text">{{favoriteText}}</span>
         </div>
       </div>
       <split></split>
       <div class="bulletin">
         <h1 class="title"></h1>
         <div class="content-wrapper border-1px">
          <p class="content">
            {{seller.bulletin}}
          </p>     
         </div>
         <ul class="supports" v-if="seller.supports">
          <li v-for="(item, index) in seller.supports" class="support-item">
          <span class="icon" :class="classMap[seller.supports[index].type]"></span>
          <span class="text">{{seller.supports[index].description}}</span>
          </li>
         </ul>
       </div>
       <split></split>
       <div class="pics">
         <h1 class="title"></h1>
         <div class="pic-wrapper" ref="picWrapper">
           <ul class="pic-list" ref="picList">
             <li class="pic-item" v-for="pic in seller.pics">
               <img :src="pic" width="120" height="90">
             </li>
           </ul>
         </div>
       </div>
       <split></split>
       <div class="info">
         <div class="title border-1px"></div>
         <ul>
           <li class="info-item" v-for="info in seller.infos">{{info}}</li>
         </ul>
       </div>
     </div>
   </div>
</template>
<script>
import star from 'components/star/star';
import split from 'components/split/split';
import BScroll from 'better-scroll';
import {saveToLocal, loadFromLocal} from '../../common/js/store';

export default {
  props: {
    seller: {
      type: Object
    }
  },
  data() {
    return {
      favorite: (() => {
        return loadFromLocal(this.seller.id, 'favorite', false)
      })()
    }
  },
  computed: {
    favoriteText() {
      return this.favorite ? '':'';
    }
  },
  created() {
      this.classMap = ['decreace', 'discount', 'special', 'invoice', 'guarantee'];

  },
  watch: {
    'seller'() {
    this.$nextTick(() => {
       this._initScroll();
       this._initPics();
    })
   }
  },
  mounted() {
    this.$nextTick(() => {
       this._initScroll();
       this._initPics();
    })
  
  }
  ,
  methods: {
    _initScroll() {
      if(!this.scroll) {
        this.scroll = new BScroll(this.$refs.seller, {
            click: true
          });   
      } else {
        this.scroll.refresh();
        
      }   
    },
    _initPics() {
      if(this.seller.pics) {
        let picWidth = 120;
        let margin = 6;
        let width = (picWidth + margin) * this.seller.pics.length -margin;
        this.$refs.picList.style.width = width + 'px';
        
          if(!this.picScroll) {
            this.picScroll = new BScroll(this.$refs.picWrapper, {
              scrollX: true
            });
          } else {
            this.picScroll.refresh();
          }
      } 
    },
      toggoleFavorite(event) {
        if(!event._constructed) {
          return;
        }
        this.favorite = !this.favorite;
        saveToLocal(this.seller.id, 'favorite', this.favorite);
      } 
  },
  
  components: {
    star,
    split
  }
}
</script>
<style lang="stylus" rel="sylesheet/stylus">
@import "../../common/stylus/mixin.styl";
  .seller
    position: absolute
    top: 174px
    bottom: 0
    left: 0
    width: 100%
    overflow: hidden
    .overview
      padding: 18px
      position: relative
      .title
        margin-bottom: 8px
        line-height: 14px
        color: rgb(7, 17, 27)
        font-size: 14px
      .desc
        padding-bottom: 18px  
        border-1px(rgba(7, 17, 27, 0.1))
        font-size: 0
        .star
          display: inline-block
          margin-right: 8px
          vertical-align: top
        .text
          margin-right: 12px
          display: inline-block
          line-height: 18px
          vertical-align: top
          font-size: 10px
          color: rgb(77, 85, 93)
      .remark
        display: flex
        padding-top: 18px
        .block
          flex: 1
          text-align: center
          border-right: 1px solid rgba(7, 17, 27, 0.1)
          &:last-child
            border: none
          h2
            margin-bottom: 4px
            line-height: 10px
            font-size: 10px
            color: rgb(147, 153, 159)
          .content
            line-height: 24px
            font-size: 10px
            color: rgb(7, 17, 27)
            .stress
              font-size: 24px
              font-weight: 200
              line-height: 24px
      .favorite
        position: absolute
        width: 50px
        right: 11px
        top: 18px
        text-align: center
        .icon-favorite
          display: block
          margin-bottom:4px
          line-height: 24px
          font-size: 24px
          color: #d4d6d8
          &.active
            color: rgb(240, 20, 20)
        .text
          line-height: 10px
          font-size: 10px
          color: rgb(77, 85, 93)
          
    .bulletin
      padding: 18px 18px 0 18px
      .title
        margin-bottom: 8px
        line-height: 14px
        color: rgb(7, 17, 27)
        font-size: 14px
      .content-wrapper
        padding: 0 12px 16px 12px
        border-1px(rgba(7, 17, 27, 0.1))
        .content
          line-height: 24px
          font-size: 12px
          color: rgb(240, 20, 20)
      .supports
        .support-item
          padding: 16px 12px
          border-1px(rgba(7, 17, 27, 0.1))
          font-size: 0
          &:last-child
            border-none()
        .icon
          display: inline-block
          width: 16px
          height: 16px
          margin-right: 6px
          vertical-align: top
          background-size: 16px 16px
          background-repeat: no-repeat
          &.decreace
              bg-image: ('decrease_4')
            &.discount
              bg-image: ('discount_4')
            &.guarantee
              bg-image: ('guarantee_4')
            &.invoice
              bg-image: ('invoice_4')
            &.special
              bg-image: ('special_4')
        .text
          font-size: 12px
          line-height: 16px
          color: rgb(7, 17, 27)
    .pics
      padding: 18px
      .title
        margin-bottom: 12px
        line-height: 14px
        color: rgb(7, 17, 27)
        font-size: 14px
      .pic-wrapper
        width:100%
        overflow: hidden
        white-space: nowrap
        .pic-list
          font-size: 0
          .pic-item
            display: inline-block
            margin-right: 6px
            width: 120px
            height: 90px
            &:last-child
              margin: 0
    .info
      padding: 18px 18px 0 18px
      .title
        padding-bottom: 12px
        line-height: 14px
        border-1px(rgba(7, 17, 27, 0.1))
        font-size: 14px
      .info-item
        padding: 16px 0 16px 12px
        line-height: 16px
        color: rgb(7, 17, 27)
        font-size: 12px 
        &:last-child
          border-none()
</style>



