<template>
  <div class="header">
     <div class="content-wrapper">
      <div class="avatar">
        <img width="64" height="64" :src="seller.avatar">
      </div>
       <div class="content">
       	 <div class="title">
       	   <span class="brand"></span>
           <span class="name">{{ seller.name }}</span>
         </div>
       	 <div class="description">
       		{{ seller.description }}/{{ seller.deliveryTime }}
       	 </div>
       	 <div v-if="seller.supports" class="support">
       	   <span class="icon" :class="classMap[seller.supports[0].type]"></span>
       	   <span class="text">{{ seller.supports[0].description }}</span>
       	 </div>    
       </div> 
       <div v-if="seller.supports" class="support-count" @click="showDetail">
         	<span class="count">{{seller.supports.length}}</span>
         	<i class="icon-keyboard_arrow_right"></i>
         </div>
      </div>
      <div class="bulletin-wrapper"  @click="showDetail">
      	<span class="bulletin-title">  	 
      	</span>
      	<span class="bulletin-text">{{seller.bulletin}}</span>
      	<span class="icon-keyboard_arrow_right"></span>
      </div>
      <div class="backgound">
      	<img :src="seller.avatar" width="100%" height="100%">
      </div>
      <div class="detail" v-show = "detaliShow">
        <div class="detail-wrapper clearfix">
        	<div class="detail-main">
        		<h1 class="name">{{ seller.name }}</h1>
        		<div class="starwrapper">
        			<star :size="48" :score="seller.score">
        		    </star>
        		</div>
        		<div class="title">
        			<div class="line"></div>
        			<div class="text"></div>
        			<div class="line"></div>
        		</div>
        		<ul class="supports" v-if="seller.supports">
        			<li v-for="(item, index) in seller.supports" class="support-item">
        				<span class="icon" :class="classMap[seller.supports[index].type]"></span>
        				<span class="text">{{seller.supports[index].description}}</span>
        			</li>
        		</ul>
        		<div class="title">
        			<div class="line"></div>
        			<div class="text"></div>
        			<div class="line"></div>
        		</div>
        	    <div class="bulletin">
        	    	<p class="content">{{seller.bulletin}}</p>
        	    </div>
        	</div>      	
        </div>
        <div class="detail-close" @click="hideDetail">
        	<i class="icon-close">
        			
        	</i>
        </div>
      </div>
   </div>
</template>

<script>
import star from '../../components/star/star.vue';

  export default {
    props: {
      seller: {
        type: Object
      }
    },
    data() {
    	return {
    		detaliShow: false
    	}
    },
    methods: {
    	showDetail() {
    		this.detaliShow = !this.detaliShow
    	},
    	hideDetail() {
    		this.detaliShow = !this.detaliShow
    	}
    },
    created() {
    	this.classMap = ['decreace', 'discount', 'special', 'invoice', 'guarantee'];
    },
    components: { star }
  };
</script>
<style lang="stylus" rel="sylesheet/stylus">
@import '../../common/stylus/mixin.styl'
    .header
      position: relative
      overflow: hidden
      color: #fff
      background: rgba(7, 92, 177, 0.5)
      .content-wrapper
        padding: 24px 12px 18px 24px
        position: relative
        font-size: 0
        .avatar
           display: inline-block
           vertical-align: top
           img
           	border-radius: 2px
         .content
            display: inline-block
            margin-left: 16px
            font-size: 14px
            .title
               margin: 2px  0 8px 0
               .brand
               	 vertical-align: top
                 display: inline-block
                 width: 30px
                 height: 18px
                 bg-image: ('brand')
                 background-size: 30px 18px
                 background-repeat: no-repeat
               .name
               	 margin-left: 6px
               	 font-weight: bold
               	 line-height: 18px
            .description
               margin-bottom: 10px
               font-size: 12px
               line-height: 12px
            .support
              .icon
              	display: inline-block
              	width: 12px
              	height: 12px
              	margin-right: 4px
              	background-size: 12px 12px
              	background-repeat: no-repeat
