              	  bg-image: ('decrease_1')
              	&.discount
              	  bg-image: ('discount_1')
              	&.guarantee
              	  bg-image: ('guarantee_1')
              	&.invoice
              	  bg-image: ('invoice_1')
              	&.special
              	  bg-image: ('special_1')
              .text
              	line-height: 12px
              	font-size: 10px              	
              	vertical-align: top
         .support-count
           position: absolute
           right: 12px
           bottom: 14px
           padding: 0 8px
           height: 24px
           line-height: 24px
           border-radius: 14px
           background: rgba(0, 0, 0, 0.2)
           text-align: center
           cursor: pointer
           .count
           	 vertical-align: top
           	 font-size: 10px
           .icon-keyboard_arrow_right
             &:before
               margin-left: 2px
               font-size: 10px
               line-height: 24px
      .bulletin-wrapper
      	line-height: 28px
      	padding: 0 21px 0 12px
      	white-space: nowrap
      	overflow: hidden
      	text-overflow: ellipsis
      	position: relative
      	.bulletin-title
      	  display: inline-block
      	  width: 22px
      	  height: 12px
      	  margin-top: 8px
      	  bg-image('bulletin')
      	  background-size: 22px 12px
      	  background-repeat: no-repeat
      	.bulletin-text
      	  font-size: 10px
      	  margin: 0 4px
      	  vertical-align: top
      	  cursor: pointer
      	.icon-keyboard_arrow_right
      	  position: absolute
      	  font-size: 10px
      	  top: 10px
      	  right: 12px
      .backgound
      	position: absolute
      	top: 0
      	left: 0
      	width: 100%
      	height: 100%
      	z-index: -1
      	filter: blur(10px)
      .detail
      	position: fixed
      	top: 0
      	left: 0
      	width: 100%
      	height: 100%
      	z-index: 60
      	overflow: auto
      	background: rgba(7, 17, 27, 0.8)
      	.detail-wrapper
      	  min-height: 100%
      	  width: 100%
      	  .detail-main
      	  	margin-top: 64px
      	  	padding-bottom: 64px
      	  	.name
      	  	  font-size: 16px
      	  	  line-height: 16px
      	  	  font-weight: 700
      	  	  text-align: center
      	  	.starwrapper
      	  	  margin: 12px 0 28px
      	  	  text-align: center
      	  	  padding: 2px 0
      	  	.title
      	  	  display: flex
      	  	  width: 80%
      	  	  margin: 30px auto 24px auto
      	  	  .line
      	  	  	flex: 1
      	  	  	position: relative
      	  	  	top: -6px
      	  	  	border-bottom: 1px solid rgba(255, 255, 255, 0.2)
