      	  		  	display: inline-block
      	  		  	width: 16px
      	  		  	height: 16px
      	  		  	vertical-align: top
      	  		  	margin-right: 6px
      	  		  	background-size: 16px 16px
      	  		  	background-repeat: no-repeat
      	  		  	&.decreace
      	  		  	  bg-image: ('decrease_2')
      	  		  	&.discount
      	  		  	  bg-image: ('discount_2')
      	  		  	&.guarantee
      	  		  	  bg-image: ('guarantee_2')
      	  		  	&.invoice
      	  		  	  bg-image: ('invoice_2')
      	  		  	&.special
      	  		  	  bg-image: ('special_2')
      	  		  .text
      	  		  	line-height: 16px
      	  		  	font-size: 12px
      	  	.bulletin
      	  		width: 80%
      	  		margin: 0 auto
      	  		.content
      	  		padding: 0 12px
      	  		line-height: 24px
       	  		font-size: 12px
