<template>
  <transition name='come'>
    <div v-show="showFlag" class="food" ref="food">
      <div class="food-content">
        <div class="image-header">
          <img :src="food.image">
          <div class="back" @click="back">
            <i class="icon-arrow_lift"></i>
          </div>
        </div>
        <div class="content">
          <h1 class="title">{{food.name}}</h1>
          <div class="detail">
            <span class="sell-count">{{food.sellCount}}</span>
            <span class="rating">{{food.rating}}%</span>
          </div>
          <div class="price">
            <span class="now">{{food.price}}</span><span class="old" v-show="food.oldPrice">{{food.oldPrice}}</span> 
          </div>
          <div class="cartconctol-wrapper">
            <cartControl class="cartControl" :food="food"></cartControl>
          </div>
          <transition class="fade">
            <div @click.stop.prevent="addFirst" class="buy" v-show="!food.count || food.count===0"></div> 
          </transition>
        </div>
        <split v-show="food.info"></split>
        <div class="info" v-show="food.info">
          <div class="title"></div>
          <p class="text">{{food.info}}</p>
          <p></p>
        </div>
        <split></split>
        <div class="rating">
          <h1 class="title"></h1>
          <ratingselect :select-type="selectType" :only-content="onlyContent" :desc="desc" :ratings="food.ratings"></ratingselect>
          <div class="rating-wrapper">
            <ul v-show="food.ratings && food.ratings.length">
              <li v-show="needShow(rating.rateType, rating.text)" v-for="rating in food.ratings" class="rating-item border-1px">
                <div class="user">
                  <span class="name">{{rating.username}}</span>
                  <img :src="rating.avatar" width="12" height="12" class="avatar">
                  
                </div>
                <div class="time">{{rating.rateTime | formatDate}}</div>
                <p class="text">
                  <span :class="{'icon-thumb_up': rating.rateType===0,'icon-thumb_down': rating.rateType===1}"></span>{{rating.text}}
                </p>
              </li>
            </ul>
            <div class="no-rating" v-show="!food.ratings || !food.ratings.length"></div>
          </div>
        </div>
      </div>
    </div>
  </transition>
  
     
 
</template>

<script>
import BScroll from 'better-scroll';
import {formatDate} from '../../common/js/date';
import cartControl from '../cartControl/cartControl';
import split from '../split/split';
import ratingselect from '../ratingselect/ratingselect';
import Vue from "vue";
import bus from '../bus';

  // const POSITIVE = 0;
  // const NEGATIVE = 1;
  const ALL = 2;

  export default {
    props: {
      food: {
        type: Object
      }
    },
    data() {
      return {
        showFlag: false,
        selectType: ALL,
        onlyContent: false,
        desc: {
          all: '',
          positive: '',
          negative: ''
        }
      }
    },
    methods: {
      show() {
        this.showFlag = true;
        this.selectType = ALL;
        this.onlyContent = false;
        this.$nextTick(() => {
          if(!this.scroll){
            this.scroll = new BScroll(this.$refs.food, {
              click: true
            });
          } else {
            this.scroll.refresh();
          }
        })
      },
      back() {
        this.showFlag = false;
      },
      addFirst(event) {
        if(!event._constructed) {
          return;
        }
        bus.$emit('cartadd', event.target);
        Vue.set(this.food, 'count', 1);
       
      },
      needShow(type, text) {
        //onlyContenttrue
        if(this.onlyContent && !text) {
          return false;
        }
        //selectTypeALL
        if (this.selectType === ALL) {
          return true;
        } else {
          return type === this.selectType;
        }
      }
      ,
      //
      select(type) {
        this.selectType = type;
        //dom
        this.$nextTick(() => {
          this.scroll.refresh();
        });
      },
      //
      toggole() {
        this.onlyContent = !this.onlyContent;
        this.$nextTick(() => {
          this.scroll.refresh();
        });
      }
    },
    created() {
      //ratingselect
      bus.$on('select', this.select);
      bus.$on('toggole', this.toggole);
    },
    filters: {
     formatDate(time) {
     
      let date = new Date(time);
      return formatDate(date, 'yyyy-MM-dd hh:mm')
     }
    },
    components: {
      cartControl,
      split,
      ratingselect
    }
  }
</script>
<style lang="stylus" rel="sylesheet/stylus">
  @import '../../common/stylus/mixin.styl'
.food
  position: fixed
  top: 0
  left: 0
  bottom: 48px
  z-index: 30
  width: 100%
  background: #fff
  transition: all 0.2s linear
  transform: translate3d(0,0,0)
  &.come-enter, &.come-leave-active
    transform: translate3d(100%, 0, 0)
  .image-header
    position: relative
    width: 100%
    height: 0
    padding-top: 100%
    img
      position: absolute
      top: 0
      left: 0
      width: 100%
      height: 100%
    .back
      position: absolute
      top: 10px 
      left: 0
      .icon-arrow_lift
        display: block
        padding: 10px
        font-size: 20px
        color: #fff
  .content
    position: relative
    padding: 10px
    .title
      line-height: 14px
      margin-bottom: 8px
      font-size: 14px
      font-weight: 700
      color: rgb(7, 17, 27)
    .detail
      margin-bottom: 18px
      line-height: 10px
      height: 10px
      font-size: 0
      .sell-count, .rating
        font-size: 10px
        color: rgb(147, 153, 159)
      .sell-count
        margin-right: 10px
    .price
      line-height: 24px
      font-weight: 700
      .now
        margin-right: 8px
        font-size: 14px
        color: rgb(240, 20, 20)
      .old
        font-size: 10px
        color: rgb(147, 153, 159)
    .cartconctol-wrapper
      position: absolute
      right: 12px
      bottom: 12px
    .buy
      position: absolute
      right: 18px
      bottom: 18px
      z-index: 10
      height: 24px
      line-height: 24px
      padding: 0 12px
      box-sizing: border-box
      border-radius: 12px
      font-size: 12px
      color: #fff
      background: rgb(0, 160, 220)
      transition: all 0.2s linear
      opacity: 1
      &.fade-enter, &.fade-leave-active
        opacity: 0
  .info
    padding: 18px
    .title
      line-height: 14px
      margin-bottom: 6px
      font-size:14px
      color: rgb(7, 17, 27)
    .text
      line-height: 24px
      padding: 0 8px
      font-size: 12px
      color: rgb(77, 85, 93)
  .rating
    padding-top: 18px
    .title
      line-height: 14px
      margin-left: 18px
      font-size: 14px
      color: rgb(7, 17, 27)
    .rating-wrapper
      padding: 0 18px
      .rating-item
        positive: relative
        padding: 16px 0
        border-1px(rgba(7, 17, 27, 0.1))
        .user
          position: absolute
          right: 0
          top: 16px
          line-height: 12px
          font-size: 0
          .name
            display: inline-block
            margin-right: 6px
            vertical-align: center
            font-size: 10px
            color: rgb(147, 153, 159)
          .avatar
            border-radius: 50%
        .time
          margin-bottom: 6px
          line-height: 12px
          font-size: 10px
          color: rgb(147, 153, 159)
        .text
          line-height: 16px
          font-size: 12px
          color: rgb(7, 17, 27)
          .icon-thumb_up, .icon-thumb_down
            line-height: 16px
            margin-right: 4px
            font-size: 12px
          .icon-thumb_up
            color: rgb(0, 160, 220)
          .icon-thumb_down
            color: rgb(147, 153, 159)
      .no-rating
        padding: 16px 0
        font-size: 12px
        color: rgb(147, 153, 159)
</style>



