<template>
<div class="goods-wrapper">
  <div class="goods">
    <div class="menu-wrapper" ref="menuWrapper">
      <ul>
          <li v-for="(item ,index) in goods" class="menu-item" :class="{'current': currentIndex === index}" @click="selectMenu(index, $event)">
            <span class="text">
            <span v-show="item.type>0" class="icon" :class="classMap[item.type]"></span>
             {{ item.name }}
            </span>
          </li>
        </ul>  
    </div>
    <div class="foods-wrapper" ref="foodsWrapper">
      <ul>
          <li v-for="(item, index) in goods" class="food-list food-list-hook" >
            <h1 class="title">{{item.name}}</h1>
            <ul>
              <li v-for="food in item.foods" @click="selectFood(food, $event)" class="food-item border-1px">
                <div class="icon">
                  <img width="57px" height="57px" :src="food.icon">
                </div>
                <div class="content">
                  <h2 class="name">{{food.name}}</h2>
                  <p class="desc">{{food.description}}</p>
                  <div class="extra">
                    <span class="count">{{food.sellCount}}</span><span>{{food.rating}}%</span>
                  </div>
                  <div class="price">
                    <span class="now">{{food.price}}</span><span class="old" v-show="food.oldPrice">{{food.oldPrice}}</span>
                  </div>
                  <div class="cartControl-wrapper">
                    <cartControl :food="food"></cartControl>
                  </div>
                </div>
              </li>
            </ul> 
          </li>
        </ul>  
    </div>
    <shopcart ref='shopcart' :selectFoods="selectFoods" :deliveryPrice="seller.deliveryPrice" :minPrice="seller.minPrice"></shopcart>
  </div>
  <food :food="selectedFood" ref='food'></food>
</div>

</template>
<script>
import BScroll from 'better-scroll';
import shopcart from '../shopcart/shopcart';
import cartControl from '../cartControl/cartControl';
import food from '../food/food';
import bus from '../bus';
const ERR_OK = 0;

export default{
  props: {
    seller: {
      type: Object
    }
  },
  data() {
    return {
      goods: [],
      listHeight: [],
      scrollY: 0,
      selectedFood: {}
    }
  },
  computed: {
    currentIndex() {
      for (let i=0; i<this.listHeight.length; i++) {
        let height1 = this.listHeight[i];
        let height2 = this.listHeight[i + 1];
        if(!height2 || (this.scrollY >= height1 && this.scrollY < height2) ) {
          return i;
        }
      }
   
      return 0;
    },
    selectFoods() {
      let foods = [];
      this.goods.forEach((good) => {
        good.foods.forEach((food) => {
          if(food.count) {
            foods.push(food);
          }
        })
      });
      return foods;
    }
  },
  created() {
    this.classMap = ['decreace', 'discount', 'special', 'invoice', 'guarantee'];
    this.$http.get('https://easy-mock.com/mock/59ae5513e0dc6633419d4c8c/zhaote_1504597267824/data').then((response => {
      if(response.ok === true) {
        console.log(33);
        this.goods = response.body.goods;
        this.$nextTick(() => {
        this._initScroll();
        this._calculateHeight();
       });
      }
    }));
    // bus.$on('cartadd', this.cartadd1);
  },
  // beforeDestroy: function () {
  //   bus.$off('cartadd', this.cartadd);
  // },
  methods: {
    _initScroll() {
      this.menuScroll = new BScroll(this.$refs.menuWrapper,{
        click: true
      });
      this.foodsScroll = new BScroll(this.$refs.foodsWrapper,{
        click: true,
        probeType: 3
      });
      this.foodsScroll.on('scroll', (pos) => {
        this.scrollY = Math.abs(Math.round(pos.y));
      })
    },
    _calculateHeight() {
      let foodList = this.$refs.foodsWrapper.getElementsByClassName('food-list-hook');
      let height = 0;
      this.listHeight.push(height);
      for (let i = 0; i < foodList.length; i++) {
        let item = foodList[i];
        height += item.clientHeight;
        this.listHeight.push(height);
      }  
    },
    selectMenu(index, event) {
      if (!event._constructed) {
        return;
      }
      let foodList = this.$refs.foodsWrapper.getElementsByClassName('food-list-hook');
      let el = foodList[index];
      this.foodsScroll.scrollToElement(el, 100);
      console.log(index);
    },
    selectFood(food, event) {
      if (!event._constructed) {
        return;
      }
      this.selectedFood = food;
      this.$refs.food.show();
    }
  //   cartadd1(target) {
  //     this.$refs.shopcart.drop(target);
  //   }
  },
  components: { 
    shopcart,
    cartControl,
    food
  }
}
</script>
<style lang="stylus" rel="sylesheet/stylus">
@import '../../common/stylus/mixin.styl'

  .goods
    display: flex
    position: absolute
    top: 174px
    bottom: 46px
    width: 100%
    overflow: hidden
    .menu-wrapper
      flex: 0 0 80px
      width: 80px
      background: #f3f5f7
      .menu-item
        display: table
        height: 54px
        width: 56px
        padding: 0 12px
        line-height: 14px
        &.current
          position: relative
          z-index: 10
          margin-top: -1px
          background: #fff
          font-weight: 700
          border-left: 2px solid #2273c3
          .text
            border-none()
        .icon
          display: inline-block
          vertical-align: top
          width: 12px
          height: 12px
          margin-right: 2px
          background-size: 12px 12px
          background-repeat: no-repeat
          &.decreace
            bg-image: ('decrease_3')
          &.discount
            bg-image: ('discount_3')
          &.guarantee
            bg-image: ('guarantee_3')
          &.invoice
            bg-image: ('invoice_3')
          &.special
            bg-image: ('special_3')
        .text
          display: table-cell
          width: 56px
          vertical-align: middle
          border-1px(rgba(7, 17, 27, 0.1))
          font-size: 12px
    .foods-wrapper
      flex: 1
      .title
        padding: 0 14px
        font-size: 12px
        height: 26px
        line-height: 26px
        border-left: 2px solid #d9dde1
        color: rgb(147, 153, 159)
        background: #f3f5f7
      .food-item
        display: flex
        padding: 18px
        padding-bottom: 18px
        margin-bottom: 1px
        border-1px(rgba(7, 17, 27, 0.1))
        &:last-child
          border-none()
          margin-bottom: 0
        .icon
          flex: 0 0 57px
          margin-right: 10px
        .content
          flex: 1
          .name
            margin: 2px 0 8px
            line-height: 14px
            font-size: 14px
            color: rgb(7, 17, 27)
          .desc
            line-height: 12px
            font-size: 10px
            margin-bottom: 8px
            color: rgb(147, 153, 159)
          .extra
           padding-bottom: 8px
           line-height: 10px
           font-size: 10px
           color: rgb(147, 153, 159)
           .count
             margin-right: 12px
          .price
            line-height: 24px
            font-weight: 700
            .now
              margin-right: 8px
              font-size: 14px
              color: rgb(240, 20, 20)
            .old
              font-size: 10px
              color: rgb(147, 153, 159)
          .cartControl-wrapper
            position: absolute
            right: 0
            bottom: 12px
</style>



