/**
 * @param  {[type]} id    [ID]
 * @param  {[type]} key   []
 * @param  {[type]} value []
 * @return {[type]}       [localStorage]
 */
export function saveToLocal(id, key, value) {
  let seller = window.localStorage.__seller__;
  if(!seller) {
    seller = {};
    seller[id] = {};
   
  } else {
    seller = JSON.parse(seller);
    if(!seller[id]) {
      seller[id] = {};
    }
  } 
  seller[id][key] = value;
  window.localStorage.__seller__ = JSON.stringify(seller);

};
/**
 * [loadFromLocal localStorage]
 * @param  {[type]} id    [ID]
 * @param  {[type]} key   []
 * @param  {[type]} def   []
 * @return {[type]}       []
 */
export function loadFromLocal (id, key, def) {
 let seller = window.localStorage.__seller__;
 if(!seller) {
  return def;
 }
 seller = JSON.parse(seller)[id];
 if(!seller) {
  return def
 }
 let ret = seller[key];
 return ret || def;
}



